import { parsedMessageFactory } from '@/functions/git/semver/parsed-message-factory';
export function parseRawCommitMessage({ rawCommit }) {
    const message = rawCommit.message;
    const regex = /^([\p{Emoji}]?)\s*(\w+)(?:\(([^)]+)\))?:\s*(.+)$/u;
    const match = message.match(regex);
    if (!match) {
        return parsedMessageFactory();
    }
    return parsedMessageFactory({
        emoji: match[1] || null,
        type: match[2] || null,
        scope: match[3] || null,
        message: match[4] || message,
    });
}
