import { c } from '@/const/theme/c';
import { config } from '@/const/theme/config';
export function formatShellOutputLine({ text, type }) {
    let shell;
    switch (type) {
        case 'stderr':
            shell = c.shellOutputError;
            break;
        case 'none':
            shell = c.shellNoOutput;
            break;
        case 'stdout':
        default:
            shell = c.shellOutput;
    }
    const theme = {
        shell,
    };
    const segments = [c.boxBorder(config.parts.vertical), theme.shell(text)];
    return segments.join(' ');
}
