import { debug, error, info, startGroup as actionsStartGroup, endGroup as actionsEndGroup } from '@actions/core';
export function logger(message, type = 'info') {
    // if (process.env.VITEST === 'true') {
    //   return;
    // }
    switch (type) {
        case 'debug':
            debug(message);
            break;
        case 'error':
            error(message);
            break;
        case 'info':
        default:
            info(message);
    }
}
export function startGroup(name) {
    // if (process.env.VITEST === 'true') {
    //   return;
    // }
    actionsStartGroup(name);
}
export function endGroup() {
    // if (process.env.VITEST === 'true') {
    //   return;
    // }
    actionsEndGroup();
}
